import React from 'react';
import ProjectTile from '../utils/ProjectTile';
import SearchBar from '../widgets/SearchBar';
import '../styles/ProjectsPage.css'; 

const projectsData = [
  {
    id: 1,
    title: 'Project 1',
    imageSrc: './resources/images/quake2.jpg', 
    description: 'Description for Project 1',
    featured: true, // Add a 'featured' property to distinguish featured projects
  },
  {
    id: 2,
    title: 'Project 2',
    imageSrc: './resources/images/quake4.jpg', 
    description: 'Description for Project 2',
    featured: false, // Make sure to update this for other projects
  },
  // Add more projects as needed
];

const ProjectsPage = () => {
  // Filter featured projects
  const featuredProjects = projectsData.filter((project) => project.featured);
  // Filter all projects except the featured ones
  const allProjects = projectsData.filter((project) => !project.featured);

  return (
    <div className="projects-page">
      <div className="projects-page-container">
        <SearchBar /> {/* Place the SearchBar component at the top */}
        
        {/* Featured Projects Section */}
        <h2 className="section-title">Featured Projects</h2>
        <div className="projects-container">
          {featuredProjects.map((project) => (
            <ProjectTile
              key={project.id}
              title={project.title}
              imageSrc={project.imageSrc}
              description={project.description}
            />
          ))}
        </div>
        
        {/* All Projects Section */}
        <h2 className="section-title">All Projects</h2>
        <div className="projects-container">
          {allProjects.map((project) => (
            <ProjectTile
              key={project.id}
              title={project.title}
              imageSrc={project.imageSrc}
              description={project.description}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

export default ProjectsPage;
